import React, { Component } from 'react';
import { Link } from "react-scroll";
import OverlayMenu from 'react-overlay-menu';
import "animate.css/animate.min.css";
import ScrollAnimation from 'react-animate-on-scroll';

export default class Header extends Component {
  constructor(props) {
    super(props);
    this.state = { isOpen: false };
    this.toggleMenu = this.toggleMenu.bind(this);
  }

  toggleMenu() {
    this.setState({ isOpen: !this.state.isOpen });
  }

  render() {
    let resumeData = this.props.resumeData;
    return (
      <React.Fragment>
        <header className="s-header">
          <div className="header-logo">
              <Link to="home" smooth={true} spy={true}><a className="site-logo" href="#home"><img src="images/logo.png" alt="Homepage" /></a>
              </Link>
          </div>
          <nav className="header-nav-wrap">
              <ul className="header-nav">
              <li><Link to="home" smooth={true} spy={true}><a href="#home" title="home">Home</a></Link></li>
              <li><Link to="about" smooth={true} spy={true}><a href="#about" title="about">About</a></Link></li>
              <li><Link to="projects" smooth={true} spy={true}><a href="#projects" title="projects">Projects</a></Link></li>
              <li><Link to="person" smooth={true} spy={true}><a href="#person" title="person">Person</a></Link></li>
              <li><Link to="contact" smooth={true} spy={true}><a href="#contact" title="contact">Contact</a></Link></li>
              </ul>
          </nav>
          <a onClick={this.toggleMenu} className="header-menu-toggle" ><span>Menu</span></a>
          <OverlayMenu 
          open={this.state.isOpen} 
          onClose={this.toggleMenu}
          right={true}
            >
              <div>
                <br />
                <br />
                <ul><Link to="home" smooth={true} spy={true}><a onClick={this.toggleMenu} href="#home" title="home">Home</a></Link></ul>
                <ul><Link to="about" smooth={true} spy={true}><a onClick={this.toggleMenu} href="#about" title="about">About</a></Link></ul>
                <ul><Link to="projects" smooth={true} spy={true}><a onClick={this.toggleMenu} href="#projects" title="projects">Projects</a></Link></ul>
                <ul><Link to="person" smooth={true} spy={true}><a onClick={this.toggleMenu} href="#person" title="person">Person</a></Link></ul>
                <ul><Link to="contact" smooth={true} spy={true}><a onClick={this.toggleMenu} href="#contact" title="contact">Contact</a></Link></ul>
              </div>
          </OverlayMenu>
        </header>
      </React.Fragment>
    );
  }
}